/* --------- ---------- ----------- */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* FONTS & ICONS */
@font-face { font-family: 'fontello';
  src:  url(/toDoList/static/media/fontello.87aebd66.woff) format('woff'), url(/toDoList/static/media/fontello.32613961.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
.icon-trash::before { content: '\f1f8'; -webkit-transition: all .15s ease; transition: all .15s ease;} 
.icon-googleplay:before { content: '\e803'; -webkit-transition: all .15s ease; transition: all .15s ease; font-size: .5em; } 
.icon-ok-circled::before { content: '\e801'; -webkit-transition: all .15s ease; transition: all .15s ease; } 
.icon-recycle:before { content: '\f1b8'; -webkit-transition: all .15s ease; transition: all .15s ease; } 
.icon-ok-circled:hover::before { color: #7281A5; } 
.icon-googleplay:hover:before { color: #7281A5; } 
.icon-recycle:hover:before { color: #7281A5; } 
.icon-trash:hover::before { color: #7281A5; } 
[class^="icon-"]::before, [class*=" icon-"]::before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  font-size: 1.5em;
  margin-right: .2em;
  text-align: center;
  color: #8594B8;
}
/* ---------- */
* { box-sizing: border-box; }
ul {list-style-type: none;}
a {text-decoration: none; color: inherit; cursor: pointer;}
button { outline: none; border: none; background: 0; padding-left: 0; cursor: pointer;}
.container { max-width: 1200px; margin: auto; padding: 0 1rem;}

body {	background-color: #8694B9; }
#todo {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	font-family: 'Rubik', sans-serif;
	position: relative;
}
header { height: inherit; position: relative; }
/*header::after { clear: both; content: ''; display: table; }*/
main {
	position: relative;
	min-height: calc(100vh - 75px);
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-content: flex-start; 
	padding: 1rem 1.5% 60px 1.5%;
}
main ul {
	padding-left: 0;
}
footer { 
	position: absolute;
	bottom: 0;
	background-color: #687597;
	color: #fff;
	width: 100%;
	font-weight: lighter;
	font-size: .95em;
	text-align: right;
	padding: .75% 15px;
}
@media screen and (max-width: 979px){
	#todo {	max-height: auto; height: auto; }
	main { height: 100%; }
}
.task {
	padding: .5rem;
	color: #444;
	font-weight: 500;
	display: block;
	letter-spacing: .35pt;
	white-space: pre-line;
    word-break: break-word;
    padding: 15px 42.5px 15px 15px;
	line-height: 1.475;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
	position: relative;
}
.task p {
	display: inline-block;
}
.task .close-button {
	font-size: 1.1rem;
	cursor: pointer;
	display: inline-block;
	position: absolute;
	bottom: 5px;
	right: 0;  
}
.task p:hover {text-decoration: line-through;}
.columna {
	min-height: 120px;
	height: auto;
	height: initial;
	align-self: flex-start;
	/*width: 32%;*/
	background-color: #fff;
	border-bottom: 1px solid rgba(0, 0, 0, 0.1);
	box-shadow: 0 2px 3px 0 rgba(0,0,0,.15);
	border-radius: 2px;
	padding: 1.5rem 1.25rem;
	overflow: auto;
}
.columna:nth-child(1), .columna:nth-child(2) { width: 35% }
.columna:nth-child(3) { width: 26%; }
.columna h2 {color: #85899d; margin: 0; }
@media screen and (max-width: 979px){
	.columna { width: 100%!important; min-height: 170px; margin-bottom: 1.5rem; }
}
#add-task-box {
	position: relative; /*absolute*/
	top: -2px;
	left: -2px;
	width: 100%;
	font-family: 'Rubik', sans-serif;
	display: flex;
	height: 75px;
}
#add-task-box > div {
	width: 100%;
	padding: 0;
}
#add-task {
	position: relative;
	right: 0;
	width: 20%;
	max-width: 200px;	
	font-size: 1em;
	padding: 5px;
	font-family: 'Rubik', sans-serif;
	letter-spacing: 1pt;
	font-weight: bold;
/*	background-color: #01579b;*/
	background-color: #687597;
	color: #fff; 
	box-shadow:inset 0 1px 5px 1px rgba(0,0,0,.1), 0 1px 5px 1px rgba(0,0,0,.15);
}
#text-task {
	margin: 0;
	height: 100%;
	padding: 20px 25px;
	font-size: 1.55rem;
	color: #85899d;
	outline: none;
	border: none;
	width: 100%;
	box-shadow:inset 0 1px 5px 1px rgba(0,0,0,.1);
}
#text-task::-webkit-input-placeholder {
	color: #ddd;
	font-style: italic;
	font-size: .775em;
}
#text-task:-ms-input-placeholder {
	color: #ddd;
	font-style: italic;
	font-size: .775em;
}
#text-task::-ms-input-placeholder {
	color: #ddd;
	font-style: italic;
	font-size: .775em;
}
#text-task::placeholder {
	color: #ddd;
	font-style: italic;
	font-size: .775em;
}
/* BOTTOM BARS  */
#add-task-box .bar { position:relative; display:block; width:100%; }
#add-task-box .bar:before, #add-task-box .bar:after {
  content:'';
  height:3px; 
  width:0;
  bottom: .1px; 
  position:absolute;
/*  background:#01579b;     7282A5        #687597*/
  background-color: #687597;
  transition:0.2s ease all; 
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}

#add-task-box .bar:before { left:50%; }

#add-task-box .bar:after { right:50%; }
#add-task-box input:focus ~ .bar:before, #add-task-box input:focus ~ .bar:after {
  width:50%;
}
@media screen and (max-width: 767px){
	#add-task {
		font-size: .85em;
	}
}

